/* global Parse */
import Parse from '../library/parse'
import { parseISO } from "date-fns";

const queryString = require("query-string");

export const withDirection = (arr) =>
  ["SE", "S", "SW", "E", "", "W", "NE", "N", "NW"].map((e, i) => ({
    ...arr[i],
    direction: e,
  }));

export const isBrowser = typeof window !== `undefined`;

export const getQueryString = () =>
  isBrowser && queryString.parse(window && window.location.search);

export const assign = (url) => isBrowser && window.location.assign(url);

export const parseStringDate = (str, fallback) =>
  isNaN(parseISO(str).getTime()) ? fallback : parseISO(str);

export const chunk = (arr, len) => {
  var chunks = [],
    i = 0,
    n = arr.length;

  while (i < n) {
    chunks.push(arr.slice(i, (i += len)));
  }

  return chunks;
};

export const options = {
  method: "post",
  headers: {
    "X-Parse-Application-Id": "32hrI9O4UWscR1vOtkNt62eoLtZgyS9SUIEwp0HL",
    "X-Parse-REST-API-Key": "F6WOCLe7BqPg3LI5rOoV1YWHdANBjcKFTgnAGZ7t",
    "Content-Type": "application/json",
  },
};

export const findAllstore = async () => {
  return await Parse.Cloud.run("findAllstore");
};

export const fetchStoreById = async (objectId) => {
  const result = await Parse.Cloud.run("findStoreById", {
    objectId,
  });
  if (result.length > 0) {
    return result[0];
  }
  return null;
};

export const cloneJSONKey = (json, allowKey) => {
  return allowKey.reduce((p, n) => {
    p[n] = json[n]
    return p;
  }, {})
}

export const fetchStores = () => {
  return Parse.Cloud.run("findstore");
};

export const fetchProducts = (storeId) => {
  return Parse.Cloud.run("findAvailableProductForStore", {
    storeId
  });
};

export const bid = (productId, bidPrice) => {
  return Parse.Cloud.run("bid", {
    modal: {
      productId,
      bidPrice,
    }
  });
}
