/* global Parse, FB */
import Parse from '../vendors/parse.min.js';
import { isBrowser } from '../helpers/'
Parse.initialize(process.env.B4A_APP_ID, process.env.B4A_JS_KEY);
Parse.serverURL = process.env.B4A_SERVER_URL
if(typeof window !== `undefined`) {
  if(window.FB) {
    Parse.FacebookUtils.init({ // this line replaces FB.init({
      appId      : '2479203505722557', // Facebook App ID
      cookie     : true,  // enable cookies to allow Parse to access the session
      xfbml      : true,  // initialize Facebook social plugins on the page
      version    : 'v9.0' // point to the latest Facebook Graph API version, found in FB's App dashboard.
    });
  } else {
    window.fbAsyncInit = () => {
      Parse.FacebookUtils.init({ // this line replaces FB.init({
        appId      : '2479203505722557', // Facebook App ID
        cookie     : true,  // enable cookies to allow Parse to access the session
        xfbml      : true,  // initialize Facebook social plugins on the page
        version    : 'v9.0' // point to the latest Facebook Graph API version, found in FB's App dashboard.
      });
    }
  }

}

export default Parse;

export const createOneProduct = (modal, storeId) => {
  Parse.Cloud.run("createProduct", {
    storeId,
    modals: [modal],
  });
};

export const updateOneProduct = (modal) => {

  Parse.Cloud.run("updateProduct", {
    modal: modal,
    storeId: modal.storeId,
    productId: modal.productId,
  });
};

export const deleteOneProduct = (modal) => {

  Parse.Cloud.run("deleteOneProduct", {
    storeId: modal.storeId,
    productId: modal.productId
  });
};


export const createStore = (modal) => {
  if (modal.objectId) {
    Parse.Cloud.run("updateStore", {
      modals: [modal],
    });
  } else {
    Parse.Cloud.run("createStoreForUser", {
      modals: [modal],
    });
  }
};



export const findProduct = async (store) => {
  return (
    store &&
    (Parse.Cloud.run("findMyStoreProduct", {
      storeId: store.id,
    }))
  );
};
export const findMyStore = () => {
  return Parse.Cloud.run("findstore");
};

export const callCloud = (action, params) => {
  return Parse.Cloud.run(action, params);
}

export const crud = (className, modal, mode, suffix) => {
  if(mode === 'create') {
    return Parse.Cloud.run(`create${className}${suffix}`, {
      modals: [modal]
    });
  }
  if(mode === 'read') {
    return Parse.Cloud.run(`find${className}${suffix}`, {
      modals: [modal]
    });
  }
  if(mode === 'update') {
    return Parse.Cloud.run(`update${className}${suffix}`, {
      modals: [modal]
    });
  }
  if(mode === 'delete') {
    return Parse.Cloud.run(`delete${className}${suffix}`, {
      modals: [modal]
    });
  }
  
}

export const getLatestBidder = (productId) => {
  return Parse.Cloud.run(`auctionProduct`, {
    objectId: productId
  });
}

