/* global Parse, FB */
import React, { useEffect, useState } from "react";
import { Box, Button, Menu, Text, Footer, Layer } from "grommet";
import "../pages/index.css";
import "./i18n";
import { assign } from "../helpers";
import { Tools } from "grommet-icons";
import { navigate } from "gatsby-link";
import Parse from '../library/parse'


const logIn = () => {
  return Parse.FacebookUtils.logIn("public_profile,email", {}).then((user) => {
    if (!user.existed()) {
      FB.api("/me?fields=id,name,email,permissions", function (response) {
        user.set("username", response.email);
        user.set("email", response.email);
        user.set("fullName", response.name);
        user.save(null, {
          success: function (user) {
            Parse.Cloud.run("setUsersAcls", {
              user: user.ObjectIds,
            });
          },
          error: function (user, error) {
            alert(
              "Failed to save user to database with error: " + error.message
            );
          },
        });
      });
    }
    Parse.Cloud.run("setUsersAcls", {
      user: user.ObjectIds,
    });
  });
};

const Home = ({ children }) => {
  const [accountMenu, setAccountMenu] = useState([]);
  const [username, setUsername] = useState('Guest')
  useEffect(() => {
    Parse.User.current()
      ? setUsername(Parse.User.current().get("email"))
      : setUsername("Guest");
  }, [children]);


  useEffect(() => {
    const logoutMenu = [
      {
        label: "My Store",
        onClick: () => {
          navigate("/account/store");
        },
      },
      {
        label: "My Orders",
        onClick: () => {
          navigate("/account");
        },
      },
      {
        label: "Sign Out",
        onClick: () => {
          Parse.User.logOut().then(() => {
            setUsername('Guest')
            navigate("/");
          });
          
        },
      },
    ];
    const accMenu =
      username === "Guest"
        ? [
            {
              label: "Sign in Using Facebook",
              onClick: () => {
                logIn()
                .then(() => {
                  setUsername(Parse.User.current().get("email"))
                  navigate('/')
                })
              },
            },
          ]
        : logoutMenu;
    setAccountMenu(accMenu);
  }, [username])

  return (
    <>
    <Box>
      <Box
        gridArea="header"
        direction="row"
        align="center"
        pad={{ horizontal: "medium", vertical: "small" }}
        background="brand"
      >
        <Box>
          <Button
            icon={<Tools />}
            label="BidBid"
            margin={{ right: "20px" }}
            onClick={() => {
              assign("/");
            }}
          />
        </Box>
        <Box margin={{ left: "auto" }}>
          <Menu label={username} items={accountMenu} />
        </Box>
      </Box>

      <Box align="center" justify="center" pad="small">
        <Box
          pad={{
            bottom: "64px",
          }}
          fill="horizontal"
          width={{
            min: "360px",
          }}
        >
          {children}
        </Box>
      </Box>
      <Footer margin={{ top: "auto" }} background="brand" pad="medium">
        <Text>Copyright</Text>
      </Footer>
    </Box>

    </>
  );
};

export default Home;
